const burger = ({
    burgerSelector,
    listSelector,
    activeBurgerStyle,
    activeListStle,
    dissableListStyle,
    navLinksSelector = null
}) => {
    const burger = document.querySelector(burgerSelector),
          list = document.querySelector(listSelector),
          navLinks = document.querySelectorAll(navLinksSelector)

    const hideContent = () => {
        burger.classList.remove(activeBurgerStyle);
        list.classList.remove(activeListStle);
        list.classList.add(dissableListStyle);
    }

    const showContent = () => {
        burger.classList.add(activeBurgerStyle);
        list.classList.remove(dissableListStyle)
        list.classList.add(activeListStle);
    }
    
    burger.addEventListener('click', () => {
        if (burger.classList.contains(activeBurgerStyle)) {
            hideContent();
        } else {
            showContent();
        }
    })

    if (navLinksSelector) {
        navLinks.forEach(link => {
            link.addEventListener('click', () => {
                hideContent();
            });
        })
    }

}