
document.addEventListener('DOMContentLoaded', (e) => {
    tabs({
        tabsParentSelector: '.tarif-plan__category-list',
        itemsParentSelector: '.tarif-plan__items',
        displayStyle: 'grid'
    });

    tabs({
        tabsParentSelector: '.authorization__list',
        itemsParentSelector: '.authorization__items',
        activeStyle: 'authorization__list-item--active',
        displayStyle: 'flex'
    });

    checkbox({
        checkboxSelector: '.authorization__label',
        activeStyle: 'authorization__label--active'
    });

    burger({
        burgerSelector: '.burger',
        listSelector: '.navigation',
        activeBurgerStyle: 'burger--active',
        activeListStle: 'navigation--active',
        dissableListStyle: 'navigation--disable',
        navLinksSelector: '.navigation__link'
    });

    smoothScroll('.navigation__link');
    smoothScroll('.footer__link');

    

    const swiper = new Swiper('.swiper', {
        // Optional parameters
        loop: true,
        // If we need pagination
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
          dynamicBullets: true
        },
      
        mousewheel: {
          sensitivity: 1,
          eventsTarget: '.slider__item'
        },
        autoplay: {
          delay: 2000,
          disableOnInteraction: false
        },
        preloadImages: false,
        lazy: {
          loadOnTransitionStart: false,
          loadPrevNext: true
        },
        watchSlidesProgress: true,
        watchSlidesVisibility: true,
        slidesPerView: 'auto',
        spaceBetween: 30,
        centeredSlides: true
      });
    
})