const tabs = ({
    tabsParentSelector,
    itemsParentSelector,
    activeStyle,
    displayStyle = 'block'
    }) => {
    const items = [...document.querySelector(itemsParentSelector).children],
          tabs = [...document.querySelector(tabsParentSelector).children];

        const hideTabContent = () => {
        
            items.forEach(item => {
                item.style.display = 'none';
                item.classList.remove(`show--${displayStyle}`, 'fade');
            });

            if (activeStyle) {
                tabs.forEach(item => {
                    item.classList.remove(activeStyle);
                });
            }
        }
    
        const showTabContent = (i = 0) => {
            
            items[i].classList.add(`show--${displayStyle}`, 'fade');
            if (displayStyle) {
                items[i].style.display = displayStyle;
            }
            if (activeStyle) {
                tabs[i].classList.add(activeStyle);
            }
        }
        
        hideTabContent();
        showTabContent();
        tabs.forEach(tab => {
            tab.addEventListener('click', function(e) {
            
                tabs.forEach((item, i) => {
                    if (e.target == item) {
                        hideTabContent();
                        showTabContent(i);
                    }
                
                });
        });
        })
}